.invite-answer-card {
  .card-header {
    border: var(--bs-border-width) solid var(--bs-border-color-translucent);
  }

  .card-body {
    border: var(--bs-border-width) solid var(--bs-border-color-translucent);
    border-top: 0;
    border-bottom-left-radius: var(--bs-border-radius);
    border-bottom-right-radius: var(--bs-border-radius);
  }
}
